<template>
  <div>
    <div style="display: flex;justify-content: center;margin-top: 100px">
      <el-card style="width: 400px">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <!--<table>
          <tr>
            <td></td>
            <td>
              <el-input type="tel" v-model="user.phonenumber" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="user.username" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input v-model="user.email" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input type="password" v-model="user.password1" placeholder="" ></el-input>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input type="password" v-model="user.password2" placeholder="" @keydown.enter.native="doLogin"></el-input>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <el-button style="width: 350px" type="primary" @click="doRegister"></el-button>
            </td>
          </tr>
        </table>-->
        <el-form ref="infoForm" :model="user" :rules="rules"  :validate-on-rule-change="true">
          <el-form-item label="" prop="phonenumber" required="true">
            <el-input
                type="tel"
                placeholder=""
                v-model="user.phonenumber"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="username" required="true">
            <el-input
                placeholder=""
                v-model="user.username"
                maxlength="15"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="email" required="true">
            <el-input
                type="email"
                placeholder=""
                v-model="user.email"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="password1" required="true">
            <el-input
                type="password"
                placeholder=""
                v-model="user.password1"
                maxlength="20"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="password2" required="true">
            <el-input
                type="password"
                placeholder=""
                v-model="user.password2"
                maxlength="20"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item prop="Intro">
            <el-row>
              
              <el-input
                type="textarea"
                style="width: 400px;"
                placeholder="15"
                :autosize="{ minRows: 2, maxRows: 4}"
                v-model="user.Intro">
              </el-input>
            </el-row>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" style="width: 100%"  @click="doRegister"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import {postNewUser} from '../../api/api.js'
export default {
  data () {
    return {
      user: {
        phonenumber: '',
        username: '',
        password1: '',
        password2: '',
        email: '',
        Intro:''
      }
    }
  },
  rules: {
    phonenumber: [//11
      {required: true, message: '', trigger: 'blur'},
      {min: 11, max: 11, message: '11', trigger:'blur'}
    ],
    username: [//
      {required: true, message: '', trigger: 'blur'}
    ],
    password1: [//4
      {required: true, message: '', trigger: 'blur'},
      {min: 4, max: 18, message: '4~18', trigger:'blur'}
    ],
    password2: [//
      {required: true, message: '', trigger: 'blur'},
    ],
    email: [//
      {required: false, message: '', trigger: 'blur'}
    ]
  },
  methods: {
    doRegister () {
      this.$refs.infoForm.validate((valid) => {
        if (valid && this.user.password1 === this.user.password2) {
          postNewUser(this.user.username, this.user.password1, this.user.password2, this.user.phonenumber, this.user.email)
            .then(response => {
              console.log(response)
              this.$message({
                message: '',
                type: 'success'
              })
              this.$router.push({ path: '/Login' })
            }).catch(error => {
              console.log(error)
              if(error.code === 400){
                this.$message({
                  message: '',
                  type: 'error'
                })
              }
              else{
                this.$message({
                  message: '',
                  type: 'error'
                })
              }
            })
        }
      })
    }
  }
}
</script>



